.style__container--iWPZv {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    height: 100%;
    width: 100%;
}

.style__imageBackground--2CK9x {
    position: absolute;
    box-sizing: border-box;
    right: 0;
    z-index: -2;
    min-width: 60%;
    height: 100vh;
    background: url(/rozz_lush/static/media/mainBackground.65738c14.png) no-repeat;
    background-size: cover;
    -webkit-animation: style__slide-left--16Mia 1s;
            animation: style__slide-left--16Mia 1s;
}

.style__imageBackground--2CK9x::before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 0;
    height: 0;
    border-top: 100vh solid white;
    border-right: 280px solid transparent;
}

@-webkit-keyframes style__slide-left--16Mia{
    0%{
        -webkit-transform:translateX(700px);
        transform:translateX(700px)
    }
    100% {
        -webkit-transform: translateX(0);
        transform:translateX(0)
    }
}

@keyframes style__slide-left--16Mia{
    0%{
        -webkit-transform:translateX(700px);
        transform:translateX(700px)
    }
    100% {
        -webkit-transform: translateX(0);
        transform:translateX(0)
    }
}

