.style__button--1YPnY {
    height: 60px;
    min-width: 210px;
    border-radius: 30px;
    font-weight: bold;
    font-size: 18px;
    color: white;
    outline: none;
    background-color: #9F65A6;
    -webkit-transition: background-color 0.2s ease-in, color 0.2s ease-in;
    transition: background-color 0.2s ease-in, color 0.2s ease-in;
}
.style__button--1YPnY:first-of-type {
    margin-bottom: 18px;
}

.style__button--1YPnY:hover,
.style__button--1YPnY:active {
    color: #9F65A6;
    background-color: white;
    border: 2px solid #9F65A6;
    cursor: pointer;
}
