.style__customLink--3j-09 {
    margin-right: 50px;
    text-decoration: none;
}

.style__customLink--3j-09:last-of-type {
    margin-right: 0;
}

.style__whiteLink--3SMkW {
    color: #fff;
}
.style__blackLink--15lVU {
    color: #000;

}
.style__activeLink--twv0x {
    color: #9F65A6;
}

.style__activeLink--twv0x,
.style__blackLink--15lVU,
.style__whiteLink--3SMkW {
    position: relative;
    opacity: 0.85;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.style__whiteLink--3SMkW:hover,
.style__blackLink--15lVU:hover,
.style__activeLink--twv0x:hover{
    opacity: 1;
}
.style__whiteLink--3SMkW::before {
    background-color: white;
}
.style__blackLink--15lVU::before {
    background-color: #000;
}
.style__activeLink--twv0x::before {
    background-color: #9F65A6;
}
.style__whiteLink--3SMkW::before,
.style__blackLink--15lVU::before,
.style__activeLink--twv0x::before {
    position: absolute;
    content: '';
    left: 0;
    bottom: -5px;
    height: 2px;
    -webkit-animation: style__underline--2Ryzf 1s;
            animation: style__underline--2Ryzf 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.style__whiteLink--3SMkW:hover::before,
.style__blackLink--15lVU:hover::before,
.style__activeLink--twv0x:hover::before {
    left: 50%;
    width: 0;
    -webkit-animation: style__underline-delete--1ITrC 0.5s;
            animation: style__underline-delete--1ITrC 0.5s;
}

@-webkit-keyframes style__underline--2Ryzf {
    0%{
        left: 50%;
        width: 0;
    }
    100% {
        left: 0;
        width: 100%;
    }
}

@keyframes style__underline--2Ryzf {
    0%{
        left: 50%;
        width: 0;
    }
    100% {
        left: 0;
        width: 100%;
    }
}

@-webkit-keyframes style__underline-delete--1ITrC {
    0%{
        left: 0;
        width: 100%;
    }
    100% {
        left: 50%;
        width: 0;
    }
}

@keyframes style__underline-delete--1ITrC {
    0%{
        left: 0;
        width: 100%;
    }
    100% {
        left: 50%;
        width: 0;
    }
}



